/* eslint-disable react/no-children-prop */
import React, { useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import { useLocation } from 'react-router-dom'
import axios from "axios";
import { Button, ButtonToolbar } from 'reactstrap';
import { Field, reduxForm } from 'redux-form';
import TextField from '@material-ui/core/TextField';
import renderFileInputField from '../../../../shared/components/form/FileInput';
import renderSelectField from '../../../../shared/components/form/Select';
import EyeIcon from 'mdi-react/EyeIcon';
import validate from '../../../Form/FormValidation/components/validate';

const renderTextField = ({
  input, label, meta: { touched, error }, children,
}) => (
  <TextField
    className="material-form__field"
    label={label}
    error={touched && error && <span className="form__form-group-error">{error}</span>}
    children={children}
    value={input.value}
    onChange={(e) => {
      e.preventDefault();
      input.onChange(e.target.value);
    }}
  />
);

renderTextField.propTypes = {
  input: PropTypes.shape().isRequired,
  label: PropTypes.string,
  meta: PropTypes.shape({
    touched: PropTypes.bool,
    error: PropTypes.string,
  }),
  children: PropTypes.arrayOf(PropTypes.element),
};

renderTextField.defaultProps = {
  meta: null,
  label: '',
  children: [],
};

const renderField = ({
  input, placeholder, type, meta: { touched, error },
}) => (
  <div className="form__form-group-input-wrap">
    <input {...input} placeholder={placeholder} type={type} />
    {touched && error && <span className="form__form-group-error">{error}</span>}
  </div>
);

renderField.propTypes = {
  input: PropTypes.shape().isRequired,
  placeholder: PropTypes.string,
  type: PropTypes.string,
  meta: PropTypes.shape({
    touched: PropTypes.bool,
    error: PropTypes.string,
  }),
};

renderField.defaultProps = {
  placeholder: '',
  meta: null,
  type: 'text',
};

const ProfileSettings = ({ handleSubmit, pristine, reset, submitting }) => {

  const [isPasswordShown, setIsPasswordShown] = useState(false);

  const showPassword = () => {
    setIsPasswordShown(!isPasswordShown);
  };
  return (
    <form className="form form--horizontal" onSubmit={handleSubmit}>
      <div className="form__form-group">
        <span className="form__form-group-label">Name</span>
        <div className="form__form-group-field">
          <Field
            name="name"
            component={renderField}
            type="text"
            placeholder="Name"
          />
        </div>
      </div>
      <div className="form__form-group">
        <span className="form__form-group-label">E-mail</span>
        <div className="form__form-group-field">
          <Field
            name="email"
            component={renderField}
            type="email"
            placeholder="example@mail.com"
          />
        </div>
      </div>

      <div className="form__form-group">
        <span className="form__form-group-label">Password</span>
        <div className="form__form-group-field">
          <Field
            name="edit_password"
            component={renderField}
            type={isPasswordShown ? 'text' : 'password'}
            placeholder="Password"
          />
          <button
            type="button"
            className={`form__form-group-button${isPasswordShown ? ' active' : ''}`}
            tabIndex="-1"
            onClick={() => showPassword()}
          >
            <EyeIcon />
          </button>
        </div>
      </div>
      <div className="form__form-group">
        <span className="form__form-group-label">Confirm-Password</span>
        <div className="form__form-group-field">
          <Field
            name="edit_confirmpassword"
            component={renderField}
            type={isPasswordShown ? 'text' : 'password'}
            placeholder="Confirm-Password"
          />
          <button
            type="button"
            className={`form__form-group-button${isPasswordShown ? ' active' : ''}`}
            tabIndex="-1"
            onClick={() => showPassword()}
          >
            <EyeIcon />
          </button>
        </div>
      </div>

      <div className="form__form-group">
        <span className="form__form-group-label">
          Add Image
    </span>
        <div className="form__form-group-field">
          <Field
            name="file"
            component={renderFileInputField}
          />
        </div>
      </div>
      <ButtonToolbar className="form__button-toolbar">
        <Button color="primary" type="submit">Submit</Button>
        <Button type="button" onClick={reset} disabled={pristine || submitting}>
          Cancel
    </Button>
      </ButtonToolbar>
    </form >
  );
}

ProfileSettings.propTypes = {
  handleSubmit: PropTypes.func.isRequired,
  reset: PropTypes.func.isRequired,
  pristine: PropTypes.bool.isRequired,
  submitting: PropTypes.bool.isRequired,
};

export default reduxForm({
  form: 'profile_settings_form', // a unique identifier for this form
  validate,
})(ProfileSettings);
