import React, { Component } from 'react';
import { Col, Container, Row,Spinner } from 'reactstrap';
import ProfileMain from './components/ProfileMain';
import ProfileCalendar from './components/ProfileCalendar';
import ProfileTasks from './components/ProfileTasks';
import ProfileTabs from './components/ProfileTabs';
import { ToastContainer, toast } from "react-toastify";
import { render } from 'react-dom';
// import axios from "axios";
import axios from '../../axios'

class Calendar extends Component {

  constructor(props) {
    super(props)
    console.log("props", props)
    this.state = {
      users: [],
    };
    this.showResults = this.showResults.bind(this);
  }

  showResults(values) {
    let id = this.props.location.state.id
    let formData = new FormData();
    if (values.file) {
      formData.append('image', values.file.file);
    }
    console.log(values.edit_password)
    console.log(values.edit_confirmpassword)

    formData.append('name', values.name);
    formData.append('email', values.email);
    formData.append('password', values.edit_password);
    formData.append('confirm_password', values.edit_confirmpassword);
    // console.log(formData);

    axios
      .post(`users/${id}/update/`,
        formData,
        {
          headers: {
            // "Authorization": "YOUR_API_AUTHORIZATION_KEY_SHOULD_GOES_HERE_IF_HAVE",
            "Content-type": "multipart/form-data",
          },
        }
      )
      // headers: {
      //   authorization: "Token " + token,
      // },
      // })
      .then(async (res) => {
        // console.log("RESPONSE", res.data.status);
        if (res.data.status === 1) {
          console.log(res.data.message)
          toast(res.data.message, {
            position: "top-right",
            autoClose: 5000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
          });
          this.props.history.push('/tables/user_list')

        } else if (res.data.status === 0) {
          // console.log(res.data.message)
          toast(res.data.message, {
            position: "top-right",
            autoClose: 5000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
          });
        }
      })
      .catch((err) => {
        console.log("ERROR", err);
      });
  }

  componentDidMount() {
    let id = ""
    if (this.props.location.state) {
      id = this.props.location.state.id
    }
    else {
      id = parseInt(localStorage.getItem("id"));
    }
    axios
      .get(`users/${id}`)
      .then((res) => {
        // console.log("response", res);
        this.setState({
          users: res.data.data,
        })
        console.log("users", this.state.users.length);

      });



  }




  render() {
    return (
      <Container>
        <div className="profile">
          <Row >
            <Col md={12} lg={12} xl={4}>
              <Row>
                {this.state.users.email ? <ProfileMain name={this.state.users.first_name} email={this.state.users.email} image={this.state.users.image} subscribed={this.state.users.plan}  /> : ""}
                {this.state.users.email ? <ProfileTasks name={this.state.users.first_name} email={this.state.users.email} image={this.state.users.image} subscribed={this.state.users.plan}  /> : ""}
              </Row >
            </Col>
            {this.state.users.email ? <ProfileTabs showResults={this.showResults} name={this.state.users.first_name} email={this.state.users.email} /> : <Spinner style={{color:'#4ce1b6'}}/>}
          </Row>
        </div>
      </Container>
    )
  }

}

export default Calendar;
