import React from 'react';
import { useTranslation } from 'react-i18next';
import { Col, Container, Row } from 'reactstrap';
import SubscriptionForm from './components/subscriptionForm';
import HorizontalFormTwo from './components/HorizontalFormTwo';
import VerticalForm from './components/VerticalForm';
import VerticalFormTwo from './components/VerticalFormTwo';
import { useLocation } from 'react-router-dom';
import axios from '../../axios';
import { ToastContainer, toast } from "react-toastify";
// import axios from "axios";
// import showResults from '../Show';




const FormValidation = (props) => {

    const [name, setName] = React.useState('')
    const [price, setPrice] = React.useState('')
    const [tag, setTag] = React.useState('')
    const [description, setDescription] = React.useState('')
    const [enter, setEnter] = React.useState(true)
    const [url, setUrl] = React.useState('services/')
    const { state } = useLocation();
    console.log("state", state)
    if (state && enter) {
        setUrl(`services/${state.id}/update/`)
        setEnter(false)
        axios
            .get(`services/${state.id}/`, {})
            .then(async (res) => {
                console.log("subsssssRESPONSE", res.data.data);
                setName(res.data.data.name)
                setPrice(res.data.data.price)
                setTag(res.data.data.tag)
                setDescription(res.data.data.description)
            })
            .catch((err) => {
                console.log("ERROR", err);
            });

    }
    const { t } = useTranslation('common');
    const showResults = (values) => {
        // const token = localStorage.getItem("token");
        axios
            .post(url, {
                name: values.name,
                description: values.sub_description,
                price: values.price,
            })
            // headers: {
            //   authorization: "Token " + token,
            // },
            // })
            .then(async (res) => {
                console.log("RESPONSE", res.data.status);
                if (res.data.status === 1) {
                    toast(res.data.message, {
                        position: "top-right",
                        autoClose: 5000,
                        hideProgressBar: false,
                        closeOnClick: true,
                        pauseOnHover: true,
                        draggable: true,
                        progress: undefined,
                    });
                    props.history.push('/tables/subscriptions')

                } else if (res.data.status === 0) {
                    toast(res.data.message, {
                        position: "top-right",
                        autoClose: 5000,
                        hideProgressBar: false,
                        closeOnClick: true,
                        pauseOnHover: true,
                        draggable: true,
                        progress: undefined,
                    });
                }
            })
            .catch((err) => {
                console.log("ERROR", err);
            });
    };
    const initialValues = {
        name: name,
        price: price,
        sub_description: description
    };
    return (
        <Container>
            <Row>
                <Col md={12}>
                    <h3 className="page-title">Add Service</h3>

                </Col>
            </Row>
            <Row>
                <SubscriptionForm onSubmit={showResults} initialValues={initialValues} props={props} />
            </Row>
        </Container>
    );
};

export default FormValidation;
