import React, { useMemo, useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { Col, Container, Row, Button, Card, CardBody,Spinner } from 'reactstrap';
import DataReactTable from './components/DataReactTable';
import { NavLink } from 'react-router-dom';
import { ToastContainer, toast } from "react-toastify";

// import axios from "axios";
import axios from '../../../containers/axios'
import CreateTableData from '../CreateData';

const DataTable = () => {
  // const reactTableData = CreateTableData();
  const { t } = useTranslation('common');
  const [data, setData] = React.useState([])
  // const [loading, setData] = React.useState([])
  useEffect(() => {
    userdata();
  }, []);

  const userdata = () => {
    const token = localStorage.getItem("token");
    axios
      .get("services/",{
        headers: {
          "Authorization": "Token "+ token,
          // "Content-type": "multipart/form-data",
        },
      })
      .then((res) => {
        console.log("response", res.data);
        setData(res.data)

      });
  }


  const columns = useMemo(
    () => [
      {
        Header: 'Id',
        accessor: '',
        disableGlobalFilter: true,
        width: 65,
      },
      {
        Header: 'Name',
        accessor: 'name',
      },
      {
        Header: 'Description',
        accessor: 'description',
        disableGlobalFilter: true,
      },

      {
        Header: 'Price',
        accessor: 'price',
        disableGlobalFilter: true,
      },
      


    ],
    [],
  );

  const deleteuser = (id) => {
    // alert(id);
    // const token = localStorage.getItem("token");
    axios
      .delete(`users/${id}/`)
      // headers: {
      //   authorization: "Token " + token,
      // },
      .then(async (res) => {
        console.log(res.data);
        if (res.data.status === 1) {
          toast(res.data.message, {
            position: "top-right",
            autoClose: 5000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
          });
          setData("");
          userdata();
          // this.useEffect();
          // this.componentDidMount();
        }
      });
  }



  const reactTableData = { tableHeaderData: columns, tableRowsData: data };

  return (
    <Container>
      <Row>
        <Col md={12} lg={12}>
          <Card>
            <CardBody>
              <div className="react-table__wrapper">
                <h3 className="page-title">Users List</h3>

                <NavLink to="/forms/form_validation">
                  <Button color="primary" type="submit">Add User</Button>
                </NavLink>
              </div>
            </CardBody>
          </Card>

        </Col>
      </Row>
      <Row style={{ width:'100%', height:'60vh', flex:1, justifyContent:'center', alignItems:'center'}}>
        {data.length > 0 ? <DataReactTable reactTableData={reactTableData} title="Users-Table" buttontitle="Add User" edit="user_edit" delete_user="delete_user" deleteuser={deleteuser} /> : <Spinner style={{color:'#4ce1b6'}} />}
      </Row>
    </Container>
  );
};

export default DataTable;
