import React from 'react';
import { useTranslation } from 'react-i18next';
import { Col, Container, Row } from 'reactstrap';
import HorizontalForm from './components/HorizontalForm';
import HorizontalFormTwo from './components/HorizontalFormTwo';
import VerticalForm from './components/VerticalForm';
import VerticalFormTwo from './components/VerticalFormTwo';
// import axios from "axios";
import { ToastContainer, toast } from "react-toastify";
import axios from '../../axios'
// import showResults from '../Show';




const FormValidation = (props) => {
  const { t } = useTranslation('common');
  const showResults = (values) => {
    // const token = localStorage.getItem("token");
    let formData = new FormData();
    if (values.file) {
      formData.append('image', values.file.file);
    }

    formData.append('name', values.name);
    formData.append('email', values.email);
    formData.append('password', values.password);
    formData.append('confirm_password', values.confirmpassword);
    axios
      .post("users/",
        formData,
        {
          headers: {
            // "Authorization": "YOUR_API_AUTHORIZATION_KEY_SHOULD_GOES_HERE_IF_HAVE",
            "Content-type": "multipart/form-data",
          },
        }
      )
      // headers: {
      //   authorization: "Token " + token,
      // },
      // })
      .then(async (res) => {
        console.log("RESPONSE", res.data.status);
        if (res.data.status === 1) {
          toast(res.data.message, {
            position: "top-right",
            autoClose: 5000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
          });
          props.history.push('/tables/user_list')

        } else if (res.data.status === 0) {
          toast(res.data.message, {
            position: "top-right",
            autoClose: 5000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
            progress: undefined,
          });
        }
      })
      .catch((err) => {
        console.log("ERROR", err);
      });
  };
  console.log(props)
  return (
    <Container>
      <Row>
        <Col md={12}>
          <h3 className="page-title">Add New User</h3>

        </Col>
      </Row>
      <Row>
        <HorizontalForm onSubmit={showResults} />
        {/* <HorizontalFormTwo onSubmit={showResults} />
        <VerticalForm onSubmit={showResults} />
        <VerticalFormTwo onSubmit={showResults} /> */}
      </Row>
    </Container>
  );
};

export default FormValidation;
