import React, { useEffect } from 'react';
import { Col, Container, Row,Spinner } from 'reactstrap';
import Editor from './components/Editor';
import axios from '../../../containers/axios'
// import axios from '../'

const TextEditor = () => {
  const [data, setData] = React.useState([])
  useEffect(() => {
    userdata();
  }, []);

  const userdata = () => {
    axios
      .get("privacy/")
      .then((res) => {
        console.log("response", res.data.data);
        setData(res.data.data.content)

      });
  }
  return (
    <Container>
      <Row>
        <Col md={12}>
          <h3 className="page-title">Privacy Policy</h3>
          <h3 className="page-subhead subhead">You can Edit here Your Privacy Policy Page
        </h3>
        </Col>
      </Row>
      <Row style={{ width:'100%', height:'60vh', flex:1, justifyContent:'center', alignItems:'center'}}> 
        {data.length > 0 ? <Editor data={data} id="2" /> : <Spinner style={{color:'#4ce1b6'}}/>}
      </Row>
    </Container>
  );
}

export default TextEditor;
