import React, { useMemo, useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { Col, Container, Row,Spinner } from 'reactstrap';
import DataReactTable from './components/DataReactTable';
// import axios from "axios";
import axios from '../../../containers/axios'
import CreateTableData from '../CreateData';

const DataTable = () => {
    // const reactTableData = CreateTableData();
    const { t } = useTranslation('common');
    const [data, setData] = React.useState([])
    useEffect(() => {
        axios
            .get("classification/", {
                // headers: {
                //     authorization: "Token 034e75861f2107686c7c141934c3f9ee79e56d88",
                // },
            })
            .then((res) => {
                console.log("response", res.data.data);
                setData(res.data.data)

            });
    }, []);


    const columns = useMemo(
        () => [
            {
                Header: 'Id',
                accessor: '',
                disableGlobalFilter: true,
                width: 65,
            },
            {
                Header: 'Fish Identification',
                accessor: 'name',
            },
            {
                Header: 'Weight (lbs)',
                accessor: 'weight',
                disableGlobalFilter: true,
            },

            {
                Header: 'User Name',
                accessor: 'user.first_name',
                disableGlobalFilter: true,
            },

            {
                Header: 'User Email',
                accessor: 'user.email',
                disableGlobalFilter: true,
            },

            // {
            //     Header: 'User Id',
            //     accessor: 'user.id',
            //     disableGlobalFilter: true,
            // },

            {
                Header: 'Created On',
                accessor: 'created_on',
                disableGlobalFilter: true,
            },
        ],
        [],
    );

    const reactTableData = { tableHeaderData: columns, tableRowsData: data };

    return (
        <Container>
            <Row>
                <Col md={12}>
                    <h3 className="page-title">LeaderBoard</h3>
                    <h3 className="page-subhead subhead">LeaderBoard-list
          </h3>
                </Col>
            </Row>
            <Row style={{ width:'100%', height:'60vh', flex:1, justifyContent:'center', alignItems:'center'}}>
                {data.length > 0 ? <DataReactTable reactTableData={reactTableData} title="LeaderBoard-Table" /> : <Spinner style={{color:'#4ce1b6'}}/>}
            </Row>
        </Container>
    );
};

export default DataTable;
