import React from 'react';
import { useTranslation } from 'react-i18next';
import { Col, Container, Row } from 'reactstrap';
import SubscriptionForm from './components/subscriptionForm';
import HorizontalFormTwo from './components/HorizontalFormTwo';
import VerticalForm from './components/VerticalForm';
import VerticalFormTwo from './components/VerticalFormTwo';
import { useLocation } from 'react-router-dom';
import OnBoardForm from '../FormValidation/components/onBoardingForm'
import axios from '../../axios'
import { ToastContainer, toast } from "react-toastify";
// import axios from "axios";
// import showResults from '../Show';




const FormValidation = (props) => {

    const [title, setTitle] = React.useState('')
    const [description, setDescription] = React.useState('')
    const [image, setImage] = React.useState('')
    const [enter, setEnter] = React.useState(true)
    const [url, setUrl] = React.useState('')
    const { state } = useLocation();
    console.log("state", state)
    if (state && enter) {
        setUrl(`onboarding/${state.id}/update/`)
        setEnter(false)
        axios
            .get(`onboarding/${state.id}/`, {})
            .then(async (res) => {
                console.log("subsssssRESPONSE", res.data.data);
                setTitle(res.data.data.title)
                setDescription(res.data.data.description)
                setImage(res.data.data.image)
            })
            .catch((err) => {
                console.log("ERROR", err);
            });

    }
    const { t } = useTranslation('common');
    const showResults = (values) => {
        console.log("image",values.onboard_image.file)
        // const token = localStorage.getItem("token");
        let formData = new FormData();
        if(values.onboard_image){
            formData.append('image', values.onboard_image.file);
        }
        
        formData.append('title', values.title);
        formData.append('description', values.description);
        axios
            .post(url,
                formData,
                {
                    headers: {
                        // "Authorization": "YOUR_API_AUTHORIZATION_KEY_SHOULD_GOES_HERE_IF_HAVE",
                        "Content-type": "multipart/form-data",
                    },
                }
            )
            // headers: {
            //   authorization: "Token " + token,
            // },
            // })
            .then(async (res) => {
                console.log("RESPONSE", res.data.status);
                if (res.data.status === 1) {
                    toast(res.data.message, {
                        position: "top-right",
                        autoClose: 5000,
                        hideProgressBar: false,
                        closeOnClick: true,
                        pauseOnHover: true,
                        draggable: true,
                        progress: undefined,
                    });
                    props.history.push('/tables/onboarding')

                } else if (res.data.status === 0) {
                    toast(res.data.message, {
                        position: "top-right",
                        autoClose: 5000,
                        hideProgressBar: false,
                        closeOnClick: true,
                        pauseOnHover: true,
                        draggable: true,
                        progress: undefined,
                    });
                }
            })
            .catch((err) => {
                console.log("ERROR", err);
            });
    };
    const initialValues = {
        title: title,
        description: description,
        image:image
    };
    return (
        <Container>
            <Row>
                <Col md={12}>
                    <h3 className="page-title">Edit OnBoard</h3>

                </Col>
            </Row>
            <Row>
                <OnBoardForm onSubmit={showResults} initialValues={initialValues} props={props} />
            </Row>
        </Container>
    );
};

export default FormValidation;
