import React, { Fragment } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import ReactTableDnDBody from './ReactTableDnDBody';
import { ThemeProps } from '../../../prop-types/ReducerProps';
import { NavLink } from 'react-router-dom';
import { ToastContainer, toast } from "react-toastify";
// import axios from "axios";
// import axios from '../../../containers/axios'
import axios from '../../../../containers/axios'

var i;
var j;
const ReactTableDefaultBody = ({ page, getTableBodyProps, prepareRow, edit, delete_user, deleteuser, deleteSubscription }) => (
  <tbody className="table table--bordered" {...getTableBodyProps()}>
    
    {
      
      i = 0,
    page.map((row) => {
      // console.log("row", row)
      prepareRow(row);
      // let i=0;
      console.log('page',page[i].id)
      
      j=parseInt(page[i++].id)+1
      
      

      return (
        <tr {...row.getRowProps()}>
          {row.cells.map(cell => (
            <td {...cell.getCellProps()}>{cell.render('Cell').props.column.Header==="Id"?j:""}{cell.render('Cell').props.column.Header==="Subscription plan"?cell.render('Cell').props.cell.value?<span className="text-success">ACTIVE</span>:<span className="text-danger">INACTIVE</span>:cell.render('Cell')}</td>
          ))}
          <td>
            <div class="d-flex align-items-center">
              {edit === "user_edit" ?
                <NavLink to={{
                  pathname: `/account/profile/`,
                  state: { id: row.original.id }
                }}
                  class="btn btn-success py-1 px-2 mr-1"
                  role="button">
                  <i class="fa fa-pencil"></i>
                </NavLink> : ""
              }

              {delete_user === "delete_user" ?
                <a
                  onClick={() => deleteuser(row.original.id)}
                  class="btn btn-danger py-1 px-2"
                  role="button"
                // onclick="deleteworkouts({{workout.id}})"
                >
                  <i class="fa fa-trash"></i>
                </a> : ""}

              {edit === "edit_subscription" ?
                <NavLink to={{
                  pathname: `/forms/subscription/${row.original.id}/`,
                  state: { id: row.original.id }
                }}
                  class="btn btn-success py-1 px-2 mr-1"
                  role="button">
                  <i class="fa fa-pencil"></i>
                </NavLink> : ""
              }

              {edit === "edit_welcome_screen" ?
                <NavLink to={{
                  pathname: `/forms/welcome-screen/${row.original.id}/`,
                  state: { id: row.original.id }
                }}
                  class="btn btn-success py-1 px-2 mr-1"
                  role="button">
                  <i class="fa fa-pencil"></i>
                </NavLink> : ""
              }

              {delete_user === "delete_subscription" ?
                <a
                  onClick={() => deleteSubscription(row.original.id)}
                  class="btn btn-danger py-1 px-2"
                  role="button"
                // onclick="deleteworkouts({{workout.id}})"
                >
                  <i class="fa fa-trash"></i>
                </a> : ""}

              {edit === "edit_onboard" ?
                <NavLink to={{
                  pathname: `/forms/onboard/${row.original.id}/`,
                  state: { id: row.original.id }
                }}
                  class="btn btn-success py-1 px-2 mr-1"
                  role="button">
                  <i class="fa fa-pencil"></i>
                </NavLink> : ""
              }
            </div>
          </td>
        </tr>
      );
    })}
  </tbody >
);

ReactTableDefaultBody.propTypes = {
  page: PropTypes.arrayOf(PropTypes.shape()).isRequired,
  getTableBodyProps: PropTypes.func.isRequired,
  prepareRow: PropTypes.func.isRequired,
};

const ReactTableBody = ({
  page, getTableBodyProps, prepareRow, withDragAndDrop, updateDraggableData, theme, edit, delete_user, deleteuser, deleteSubscription
}) => (
  <Fragment>
    {withDragAndDrop
      ? (
        <ReactTableDnDBody
          page={page}
          getTableBodyProps={getTableBodyProps}
          prepareRow={prepareRow}
          updateDraggableData={updateDraggableData}
          theme={theme}
        />
      ) : (
        <ReactTableDefaultBody
          page={page}
          getTableBodyProps={getTableBodyProps}
          prepareRow={prepareRow}
          edit={edit}
          delete_user={delete_user}
          deleteuser={deleteuser}
          deleteSubscription={deleteSubscription}
        />
      )
    }
  </Fragment>
);

ReactTableBody.propTypes = {
  page: PropTypes.arrayOf(PropTypes.shape()).isRequired,
  getTableBodyProps: PropTypes.func.isRequired,
  prepareRow: PropTypes.func.isRequired,
  updateDraggableData: PropTypes.func.isRequired,
  withDragAndDrop: PropTypes.bool.isRequired,
  theme: ThemeProps.isRequired,
};

export default connect(state => ({
  theme: state.theme,
}))(ReactTableBody);
