import React, { useState } from 'react';
import PropTypes from 'prop-types';
import { useTranslation } from 'react-i18next';
import { Field, reduxForm } from 'redux-form';
import {
    Card, CardBody, Col, Button, ButtonToolbar,
} from 'reactstrap';
import EyeIcon from 'mdi-react/EyeIcon';
import renderSelectField from '../../../../shared/components/form/Select';
import renderFileInputField from '../../../../shared/components/form/FileInput';
import validate from './validate';


const renderField = ({
    input, placeholder, type, meta: { touched, error },
}) => (
    <div className="form__form-group-input-wrap">
        <input {...input} placeholder={placeholder} type={type} />
        {touched && error && <span className="form__form-group-error">{error}</span>}
    </div>
);

renderField.propTypes = {
    input: PropTypes.shape().isRequired,
    placeholder: PropTypes.string,
    type: PropTypes.string,
    meta: PropTypes.shape({
        touched: PropTypes.bool,
        error: PropTypes.string,
    }),
};

renderField.defaultProps = {
    placeholder: '',
    meta: null,
    type: 'text',
};

const HorizontalForm = ({
    handleSubmit, pristine, reset, submitting, name, props,initialValues
}) => {
    // props.initialValues({ name: 'your name' });
    const { t } = useTranslation('common');

    const [isPasswordShown, setIsPasswordShown] = useState(false);

    const showPassword = () => {
        setIsPasswordShown(!isPasswordShown);
    };


    return (
        <Col md={12} lg={12} >
            <Card>
                <CardBody>
                    <div className="card__title">
                        <h5 className="bold-text">Edit OnBoard</h5>
                        <h5 className="subhead"></h5>
                    </div>
                    <form className="form form--horizontal" onSubmit={handleSubmit}>
                        <div className="form__form-group">
                            <span className="form__form-group-label">Title</span>
                            <div className="form__form-group-field">
                                <Field
                                    name="title"
                                    component={renderField}
                                    type="text"
                                    placeholder="Add Title..."
                                />
                            </div>
                        </div>
                        <div className="form__form-group">
                            <span className="form__form-group-label">Description</span>
                            <div className="form__form-group-field">
                                <Field
                                    name="description"
                                    component={renderField}
                                    type="text"
                                    placeholder="Add Description..."
                                />
                            </div>
                        </div>
                        <div className="form__form-group">
                            <span className="form__form-group-label">
                                Add Image
                            </span>
                            <div className="form__form-group-field">
                                <Field
                                    name="onboard_image"
                                    component={renderFileInputField}
                                />
                            </div>
                            <div className="profile__avatar">
            {initialValues.image ? <img src={"https://tripletale.worksdelight.com"+initialValues.image} /> : <img src="data:image/jpeg;base64," alt="avatar" />}

          </div>
                        </div>
                        
                        

                        <ButtonToolbar className="form__button-toolbar">
                            <Button color="primary" type="submit">Submit</Button>
                            <Button type="button" onClick={reset} disabled={pristine || submitting}>
                                Cancel
              </Button>
                        </ButtonToolbar>
                    </form>
                </CardBody>
            </Card>
        </Col>
    );
};

HorizontalForm.propTypes = {
    handleSubmit: PropTypes.func.isRequired,
    reset: PropTypes.func.isRequired,
    pristine: PropTypes.bool.isRequired,
    submitting: PropTypes.bool.isRequired,
};



export default reduxForm({
    form: 'subscription_form_validation', // a unique identifier for this form
    enableReinitialize: true,
    validate,
})(HorizontalForm);

