import React, { useMemo, useEffect, useState } from 'react';
import { useTranslation } from 'react-i18next';
import { Col, Container, Row, Button, Card, CardBody,Spinner } from 'reactstrap';
import DataReactTable from './components/DataReactTable';
// import axios from "axios";
import { NavLink } from 'react-router-dom';
import axios from '../../../containers/axios'
import CreateTableData from '../CreateData';
import { ToastContainer, toast } from "react-toastify";

const DataTable = () => {
    // const reactTableData = CreateTableData();
    const { t } = useTranslation('common');
    const [data, setData] = React.useState([])
    useEffect(() => {
        subscriptiondata();
    }, []);

    const subscriptiondata = () => {
        axios
            .get("services/")
            .then((res) => {
                console.log("response", res.data);
                setData(res.data)

            });
    }



    const deleteSubscription = (id) => {
        // alert(id);
        // const token = localStorage.getItem("token");
        axios
            .delete(`services/${id}/`)
            // headers: {
            //   authorization: "Token " + token,
            // },
            .then(async (res) => {
                console.log(res.data);
                if (res.data.status === 1) {
                    toast(res.data.message, {
                        position: "top-right",
                        autoClose: 5000,
                        hideProgressBar: false,
                        closeOnClick: true,
                        pauseOnHover: true,
                        draggable: true,
                        progress: undefined,
                    });
                    setData("");
                    subscriptiondata();
                    // this.componentDidMount();
                }
            });
    }

    const columns = useMemo(
        () => [
            {
                Header: 'Id',
                accessor: '',
                disableGlobalFilter: true,
                width: 65,
            },
            {
                Header: 'Name',
                accessor: 'name',
            },
            {
                Header: 'Price',
                accessor: 'price',
                disableGlobalFilter: true,
            },
            
            {
                Header: 'Description',
                accessor: 'description',
                disableGlobalFilter: true,
            },

        ],
        [],
    );

    const reactTableData = { tableHeaderData: columns, tableRowsData: data };

    return (
        <Container>
            <Row>
                <Col md={12} lg={12}>
                    <Card>
                        <CardBody>
                            <div className="react-table__wrapper">
                                <h3 className="page-title">Service List</h3>



                                <NavLink to="/forms/subscription">
                                    <Button color="primary" type="submit">Add Service</Button>
                                </NavLink>
                            </div>
                        </CardBody>
                    </Card>

                </Col>
            </Row>
            <Row style={{ width:'100%', height:'60vh', flex:1, justifyContent:'center', alignItems:'center'}}>
                {data.length > 0 ? <DataReactTable reactTableData={reactTableData} title="Service-Table" buttontitle="Add Service" edit="edit_subscription" delete_user="delete_subscription" deleteSubscription={deleteSubscription} /> : <Spinner style={{color:'#4ce1b6'}} />}
            </Row>
        </Container>
    );
};

export default DataTable;
